	        vpl_inicial = vpl(juros_inicial, montantes);
	        vpl_final = vpl(juros_final, montantes);
	        if (sinal(vpl_inicial) !== sinal(vpl_final)) break;
	        juros_inicial -= 1.0;
	        juros_final += 1.0;
